.details_request{
  display: -webkit-box;
  -webkit-box-orient: vertical;
  padding: 0px 20px 0px 20px;
  background: #fff;
  font-size: 12px;
  margin-bottom: 30px;
}
.details_request_title{
  display: -webkit-box;
  -webkit-box-orient: vertical;
  border-bottom: 1px solid #e6e6e6;
  padding-top: 13px;
  padding-bottom: 13px;
  color: #666666;
}

.details_request_title > .exp{
  font-weight: 600;
  color: #000000;
}
.details_request_answer{
  display: -webkit-box;
  -webkit-box-orient: vertical;
}
.details_request_answer > p{
  display: -webkit-box;
  margin-top: 4px;
}
.details_request_answer > p > img{
  width: 11px;
  margin-right: 10px;

}
.details_request_answer > p > span{
  -webkit-box-flex:1;
}